'use client';

import { createClient } from '@/lib/supabase/client';
import { useEffect, useState } from 'react';
import { Plus, Trash2, Filter } from 'lucide-react';
import { Alias, Rule } from '@/types/database';

export default function RulesPage() {
  const supabase = createClient();
  const [aliases, setAliases] = useState<Alias[]>([]);
  const [rules, setRules] = useState<Rule[]>([]);
  const [selectedAlias, setSelectedAlias] = useState<string>('');
  const [pattern, setPattern] = useState('');
  const [action, setAction] = useState<'allow' | 'block'>('block');
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    setIsLoading(true);
    const { data: aliasData } = await supabase.from('aliases').select('*');
    const { data: ruleData } = await supabase.from('rules').select('*');

    if (aliasData) setAliases(aliasData);
    if (ruleData) setRules(ruleData);
    if (aliasData && aliasData.length > 0) setSelectedAlias(aliasData[0].id);

    setIsLoading(false);
  };

  const createRule = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!pattern || !selectedAlias) return;

    const { error } = await supabase.from('rules').insert({
      alias_id: selectedAlias,
      pattern,
      action,
    });

    if (!error) {
      setPattern('');
      fetchData();
    }
  };

  const deleteRule = async (id: string) => {
    const { error } = await supabase.from('rules').delete().eq('id', id);
    if (!error) fetchData();
  };

  return (
    <div className="max-w-4xl mx-auto space-y-8">
      <h1 className="text-3xl font-bold text-gray-900">Filtering Rules</h1>

      {/* Create Rule Form */}
      <form onSubmit={createRule} className="bg-white p-6 rounded-xl shadow-sm border border-gray-100 grid grid-cols-1 md:grid-cols-4 gap-4 items-end">
        <div className="space-y-2">
          <label className="text-sm font-medium text-gray-700">Select Alias</label>
          <select
            value={selectedAlias}
            onChange={(e) => setSelectedAlias(e.target.value)}
            className="w-full p-3 rounded-lg border border-gray-200 focus:outline-none focus:ring-2 focus:ring-blue-500 bg-white"
          >
            {aliases.map(a => (
              <option key={a.id} value={a.id}>{a.address}</option>
            ))}
          </select>
        </div>

        <div className="md:col-span-2 space-y-2">
          <label className="text-sm font-medium text-gray-700">Sender Pattern (email or domain)</label>
          <div className="relative">
            <Filter className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400" size={18} />
            <input
              type="text"
              placeholder="e.g., spam.com or news@site.com"
              value={pattern}
              onChange={(e) => setPattern(e.target.value)}
              className="w-full pl-10 pr-4 py-3 rounded-lg border border-gray-200 focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          </div>
        </div>

        <button
          type="submit"
          className="bg-black text-white px-6 py-3 rounded-lg font-medium hover:bg-gray-800 transition-colors flex items-center justify-center gap-2"
        >
          <Plus size={20} />
          Add Rule
        </button>
      </form>

      {/* Rules List */}
      <div className="bg-white rounded-xl shadow-sm border border-gray-100 overflow-hidden">
        {isLoading ? (
          <div className="p-8 text-center text-gray-500">Loading rules...</div>
        ) : rules.length === 0 ? (
          <div className="p-8 text-center text-gray-500">
            No rules created yet. Add a rule above to start filtering your mail.
          </div>
        ) : (
          <table className="w-full text-left border-collapse">
            <thead>
              <tr className="bg-gray-50 border-b border-gray-100">
                <th className="px-6 py-4 font-semibold text-gray-700">Alias</th>
                <th className="px-6 py-4 font-semibold text-gray-700">Pattern</th>
                <th className="px-6 py-4 font-semibold text-gray-700">Action</th>
                <th className="px-6 py-4 font-semibold text-gray-700 text-right">Actions</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-100">
              {rules.map((rule) => {
                const alias = aliases.find(a => a.id === rule.alias_id);
                return (
                  <tr key={rule.id} className="hover:bg-gray-50 transition-colors">
                    <td className="px-6 py-4 text-sm text-gray-600">{alias?.address || 'Unknown'}</td>
                    <td className="px-6 py-4 font-medium text-gray-900">{rule.pattern}</td>
                    <td className="px-6 py-4">
                      <span className={`px-2 py-1 rounded text-xs font-bold uppercase ${
                        rule.action === 'block' ? 'bg-red-100 text-red-700' : 'bg-blue-100 text-blue-700'
                      }`}>
                        {rule.action}
                      </span>
                    </td>
                    <td className="px-6 py-4 text-right">
                      <button
                        onClick={() => deleteRule(rule.id)}
                        className="p-2 text-gray-400 hover:text-red-600 transition-colors"
                      >
                        <Trash2 size={18} />
                      </button>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
}
